export const UPDATE_COLLAPSED = 'UPDATE_COLLAPSED';
export const updateCollapsed = updates => ({ type: UPDATE_COLLAPSED, updates });

export const SET_LOADING_THREAD = 'SET_LOADING_THREAD';
export const setLoadingThread = bool =>  ({ type: SET_LOADING_THREAD, bool });



